import Vue from 'vue';
import Router from 'vue-router';
import Translate from './views/Translate.vue';
import Support from './views/Support.vue';


Vue.use(Router);

export default new Router({
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'Translate',
      component: Translate,
    },
    {
      path: '/support',
      name: 'Support',
      component: Support,
    },
  ],
});
